import axios from "axios";

export const API = {
    getTasks() {
        return axios.get(`https://calm-plateau-74946.herokuapp.com/tasks`);
    },
    addTask(taskTitle) {
        return axios.post(`https://calm-plateau-74946.herokuapp.com/tasks`,
            {
                title: taskTitle
            });
    },
    updateTaskStatus(taskId, done) {
        return axios.put(`https://calm-plateau-74946.herokuapp.com/tasks/${taskId}`,
            {
                done: done
            });
    },
    updateTaskTitle(taskId, title) {
        return axios.put(`https://calm-plateau-74946.herokuapp.com/tasks/${taskId}`,
            {
                title: title
            });
    },
    deleteTask(taskId) {
        debugger
        return axios.delete(`https://calm-plateau-74946.herokuapp.com/tasks/${taskId}`);
    }

};